.cards-field{
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: wrap;
  border-radius: 5px;
  box-sizing: border-box;
  max-width: 1200px;
}

.card-container{
  display: flex;
  align-items: center;
  justify-content: space-around;
  cursor: pointer;
  perspective: 25rem;

  &:hover > .card {
      transform: translateZ(2rem);
      box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
  }
  &.flipped > .card {
      transform: rotateY(180deg) translateX(-100%);
      transform-origin: left;
  }
}

.card{
  display: flex;
  flex-direction: column;
  margin: 15px;
  position: relative;
  width: 250px;
  height: 250px;
  transition: 0.6s;

  transform-style: preserve-3d;
  transform-origin: right;
  transition: all .5s;
  box-shadow:  0 .1rem .25rem black;
  border-radius: 5px;

  &__front,
  &__back {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      backface-visibility: hidden;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;  
  }
  &__front{
      background-color: rgba(255, 0, 0, 0);
  }
  &__back {
      background-color: gray;
      // background-image: url('../../assets/card-back.jpg');
      background-position: center;
      background-size: cover;
      border-radius: inherit;
      // transform: rotateY(180deg);
  }
  &__hr-train, &__hr-play{
    width: 100%;
    height: 20px;
  }
  &__hr-train{
    background-color: #2196F3;
  }
  &__hr-play{
    background-color: #ff0000;
  }
  &__name{

    font-family: Roboto;
    font-size: 24px;
    line-height: 36px;
    font-weight: 400;
    text-align: center;
  }
  &__image{
    top: 0;
    left: 0;
    border-radius: 5px 5px 0 0;

    height: 190px;
    backface-visibility: hidden;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
}
.display-none{
  display: none;
}
.card{
  &__image__play{
    height: 250px;
  }
}
.flipped-button{
  position: absolute;
  height: 50px;
  right: 5px;
  bottom: -5px;
}